.dropdown-menu {
    background: red;
    width: 200px;
    position: absolute;
    top: 80px;
    list-style: none;
    text-align: start;
    z-index: 30;
  }
  
  .dropdown-menu li {
    background: #074d16;
    cursor: pointer;
  }
  
  .dropdown-menu li:hover {
    background: hsl(135, 100%, 68%);
  }
  
  .dropdown-menu.clicked {
    display: none;
  }
  
  .dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 16px;
  }
  
  @media screen and (max-width: 960px) {
    /* .fa-caret-down {
      display: none;
    } */
  }
  
  @media screen and (max-width: 960px){
    .dropdown-menu{
        width: 100%;
        position:absolute;
        top:170px;
        list-style: none;
        text-align: center;
    }
  }